import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MockService } from '../../../shared/services/mock.service';

@Component({
  selector: 'app-item-mapping',
  templateUrl: './item-mapping.component.html',
  styleUrls: ['./item-mapping.component.scss']
})
export class ItemMappingComponent implements OnInit {
  data;
  filteredData;
  editable = true;
  listView = true;
  toggleViewOptions = true;

  tableData = {
    item: 'pipe',
    description: 'gol gol',
    vendorPrice: 1000,
    sellingPrice: 1500,
    maxDiscount: '10%'
  };

  searchKey = 'item';

  constructor(private router: Router, private mock: MockService) { }

  ngOnInit() {
    this.data = this.mock.listItems;
    this.filteredData = this.data;
  }

  filter(event) {
    this.filteredData = event;
  }
}
