import { Component, OnInit } from '@angular/core';
import { MockService } from '../../../shared/services/mock.service';

@Component({
  selector: 'app-vendor-mapping',
  templateUrl: './vendor-mapping.component.html',
  styleUrls: ['./vendor-mapping.component.scss']
})
export class VendorMappingComponent implements OnInit {

  filteredData;
  data;
  editable = true;
  listView = true;
  toggleViewOptions = true;
  detailData = {
    itemName: 'pipe',
    itemDescription: 'bahut bada pipe',
    totalVendors: 10,
    unit: 'mm'
  };

  tableData = {
    item: 'pipe',
    description: 'gol gol',
    vendorPrice: 1000,
    sellingPrice: 1500,
    maxDiscount: '10%'
  };

  searchKey = 'item';

  constructor(private mock: MockService) { }

  ngOnInit() {
    this.data = this.mock.listVendors;
    this.filteredData = this.data;
  }

  filter(event) {
    this.filteredData = event;
  }
}
