import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'lodash';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit {

  @Input() title: string;
  @Input() listView: boolean;
  @Input() hasDetails: Boolean = false;
  @Input() showEdit: Boolean = true;
  @Input() routeOnEdit: string;
  @Input() addButton: Boolean = true;
  @Input() deleteButton: Boolean = true;
  @Input() detailData: any;
  @Output() elementDeleted: EventEmitter<object> = new EventEmitter<object>();

  public showDetails: Boolean = false;

  constructor(private route: Router) { }

  ngOnInit() {
    this.listView = false;
  }

  get _keys() {
    return _.keys(this.detailData);
  }

  get navigate() {
    return this.route.navigate([`${this.route.url}/${this.detailData.id}`]);
  }
}
