export interface ItemStruct {
    itemId?: string;
    itemName: string;
    categoryName: string;
    subCategoryName: string;
    itemDescription: string;
    unit: string;
    propertyName1?: string;
    propertyValue1?: string;
    propertyUnit1?: string;
    propertyName2?: string;
    propertyValue2?: string;
    propertyUnit2?: string;
    propertyName3?: string;
    propertyValue3?: string;
    propertyUnit3?: string;
    propertyName4?: string;
    propertyValue4?: string;
    propertyUnit4?: string;
    propertyName5?: string;
    propertyValue5?: string;
    propertyUnit5?: string;
    propertyName6?: string;
    propertyValue6?: string;
    propertyUnit6?: string;
    propertyName7?: string;
    propertyValue7?: string;
    propertyUnit7?: string;
    propertyName8?: string;
    propertyValue8?: string;
    propertyUnit8?: string;
    propertyName9?: string;
    propertyValue9?: string;
    propertyUnit9?: string;
    propertyName10?: string;
    propertyValue10?: string;
    propertyUnit10?: string;
    propertyName11?: string;
    propertyValue11?: string;
    propertyUnit11?: string;
    propertyName12?: string;
    propertyValue12?: string;
    propertyUnit12?: string;
    propertyName13?: string;
    propertyValue13?: string;
    propertyUnit13?: string;
    propertyName14?: string;
    propertyValue14?: string;
    propertyUnit14?: string;
    propertyName15?: string;
    propertyValue15?: string;
    propertyUnit15?: string;
    createdBy?: string;
    searchStr?: string;
    updatedAt?: string;
    createdAt?: string;
}

export class Item {
    public item: ItemStruct = <ItemStruct>{};

    constructor(itemData) {
        this.item.itemName = itemData.itemName;
        this.item.categoryName = itemData.categoryName;
        this.item.subCategoryName = itemData.subCategoryName;
        this.item.itemDescription = itemData.itemDescription;
        this.item.unit = itemData.unit;
    }
}
