import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-fiter-select',
  templateUrl: './fiter-select.component.html',
  styleUrls: ['./fiter-select.component.scss']
})
export class FiterSelectComponent implements OnInit {

  @Input() filters: Array<object>;
  @Input() data: Array<object>;
  @Output() filteredData: EventEmitter<Array<object>> = new EventEmitter<Array<object>>();

  constructor() { }

  ngOnInit() {
  }

  filter() {
    const result = this.data;
    this.filteredData.emit(result);
  }
}
