import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ItemMappingComponent } from './pages/item-mapping/item-mapping.component';
import { ItemviewComponent } from './pages/itemview/itemview.component';
import { MappingViewComponent } from './pages/mapping-view/mapping-view.component';
import { VendorMappingComponent } from './pages/vendor-mapping/vendor-mapping.component';
import { VendorviewComponent } from './pages/vendorview/vendorview.component';

const adminRoutes: Routes = [
    { path: 'ManageItems', component: ItemviewComponent },
    { path: 'ManageVendors', component: VendorviewComponent },
    { path: 'ManageMappings/item', component: ItemMappingComponent },
    { path: 'ManageMappings/vendor', component: VendorMappingComponent },
    { path: 'ManageMappings/item/:id', component: MappingViewComponent },
    { path: 'ManageMappings/vendor/:id', component: MappingViewComponent }
];

@NgModule({
    imports: [RouterModule.forChild(adminRoutes)],
    exports: [RouterModule]
})

export class AdminRoutingModule { }
