import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import * as _ from 'lodash';
@Component({
  selector: 'app-fiter',
  templateUrl: './fiter.component.html',
  styleUrls: ['./fiter.component.scss']
})
export class FiterComponent implements OnInit {

  @Input() data: Array<object>;
  @Input() filters: Array<object>;
  @Input() toggleViewOptions: boolean;
  @Output() filteredData: EventEmitter<Array<object>> = new EventEmitter<Array<object>>();
  @Output() listView: EventEmitter<boolean> = new EventEmitter<boolean>();

  public filteredSelect: Array<object>;
  public view = false;

  constructor() { }

  ngOnInit() {
    this.filteredSelect = this.data;
  }

  change(event) {
    this.filterSearch(event.target.value);
  }

  filterSearch(searchString) {
    if (searchString === '') {
      this.filteredData.emit(this.data);
    }

    const result = [];
    _.forEach(this.filteredSelect, (value, index) => {
      if (this.hasString(searchString, value)) {
        result.push(value);
      }
    });
    this.filteredData.emit(result);
  }

  onFilterSelect(filteredData) {
    this.filteredSelect = filteredData;
  }

  toggleView() {
    this.view = !this.view;
    this.listView.emit(this.view);
  }

  hasString(search, object) {
    const searchString = search.toLowerCase();
    let found: Boolean = false;
    _.forOwn(object, (value, key) => {

      // join the array type to make a string and use the same for search
      if (value instanceof Array) {
        value = value.join('');
      }

      if (typeof (value) === 'string' && value.toLowerCase().includes(searchString)) {
        found = true;
      }
    });
    return found;
  }
}
