import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { AdminRoutingModule } from './admin.routing';
import { FilterItemComponent } from './components/filter-item/filter-item.component';
import { FilterVendorComponent } from './components/filter-vendor/filter-vendor.component';
import { ItemDetailsComponent } from './components/item-details/item-details.component';
import { VendorDetailsComponent } from './components/vendor-details/vendor-details.component';
import { ItemviewComponent } from './pages/itemview/itemview.component';
import { MappingViewComponent } from './pages/mapping-view/mapping-view.component';
import { VendorviewComponent } from './pages/vendorview/vendorview.component';
import { ItemMappingComponent } from './pages/item-mapping/item-mapping.component';
import { VendorMappingComponent } from './pages/vendor-mapping/vendor-mapping.component';

@NgModule({
  imports: [
    CommonModule,
    AdminRoutingModule,
    SharedModule,
    ReactiveFormsModule,
    FormsModule
  ],
  declarations: [ItemviewComponent,
    VendorviewComponent,
    ItemDetailsComponent,
    FilterItemComponent,
    FilterVendorComponent,
    VendorDetailsComponent,
    MappingViewComponent,
    ItemMappingComponent,
    VendorMappingComponent]
})
export class AdminModule { }
