import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import * as _ from 'lodash';
import { TemplateFormat } from '../../models/template';
import { FormService } from '../../services/form.service';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})

export class FormComponent implements OnInit {

  @Input() data: object;
  @Input() template: Array<TemplateFormat>;
  @Input() mode: string;
  @Output() formData: EventEmitter<object> = new EventEmitter<object>();

  public itemElement: object = {};
  public items: Array<object> = [];

  public form: FormGroup;
  constructor(private formservice: FormService) { }

  ngOnInit() {
    this.contructObjectForItemArray();
    this.form = this.createForm();
    this.formMode();
  }

  createForm() {
    const group = {};
    this.template.map(attribute => {
      group[attribute.key] = new FormControl('', Validators.required);
    });
    return new FormGroup(group);
  }

  save(data) {
    this.formData.emit(data);
  }

  formMode() {
    if (this.mode === 'show') {
      return this.form.disable();
    }
  }

  get addItem() {
    return this.items.push(this.itemElement);
  }

  get removeItem() {
    this.items.splice((this.items.length - 1));
    return this.items;
  }

  contructObjectForItemArray() {
    const arrayElement = _.find(this.template, (e) => e.dataType === 'array');

    // add the key names to dataToSave object before passing it to form array componetn
    if (arrayElement && arrayElement.hasOwnProperty('props')) {
      _.forEach(arrayElement.props, (prop) => {
        this.itemElement[prop.key] = '';
      });
    }
  }
}
