import { Component, OnInit } from '@angular/core';
import { Item, ItemStruct } from '../../../shared/models/item';
import { TemplateFormat } from '../../../shared/models/template';
import { MockService } from '../../../shared/services/mock.service';

@Component({
  selector: 'app-itemview',
  templateUrl: './itemview.component.html',
  styleUrls: ['./itemview.component.scss']
})
export class ItemviewComponent implements OnInit {

  public item: ItemStruct;
  public items: Array<ItemStruct>;
  public itemTemplate: Array<TemplateFormat>;
  public filteredData: Array<ItemStruct>;

  constructor(private mockservice: MockService) { }

  ngOnInit() {
    const newItem = new Item({ itemName: '', categoryName: '', subCategoryName: '', itemDescription: '', unit: '' });
    this.item = newItem.item;
    this.items = this.mockservice.listItems;
    this.itemTemplate = this.mockservice.getItemTemplate;
    this.filteredData = this.items;
  }

  filter(data) {
    this.filteredData = data;
  }

  save(item) {
    console.log('item');
  }
}
