import { } from '';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import * as _ from 'lodash';
import { MockService } from '../../services/mock.service';

@Component({
  selector: 'app-editable-table',
  templateUrl: './editable-table.component.html',
  styleUrls: ['./editable-table.component.scss']
})
export class EditableTableComponent implements OnInit {

  @Input() tableData: Array<any>;
  @Input() searchKey: string;
  @Input() editable = false;
  @Output() remove = new EventEmitter<object>();

  public items;
  public rowToEdit: number;

  constructor(private mock: MockService) { }

  ngOnInit() {
  }

  get keys() {
    return _.keys(this.tableData[0]);
  }

  removeLine(data) {
    this.remove.emit(data);
  }

  istypeAhead(key) {
    return key === this.searchKey;
  }

  search(event) {
    this.items = [];
    const x = this.mock.listItems; // replce by real service
    this.items = _.filter(x, o => o.itemName.toLowerCase().indexOf(event.toLowerCase()) > -1).map(o => o.itemName);
  }

  change(event) {
    this.items = [];
  }

  edit(row) {
    if (this.rowToEdit === row) return this.rowToEdit = null;

    return this.rowToEdit = row;
  }
}
