import { Component, OnInit } from '@angular/core';
import { TemplateFormat } from '../../../shared/models/template';
import { Vendor, VendorStruct } from '../../../shared/models/vendor';
import { MockService } from '../../../shared/services/mock.service';

@Component({
  selector: 'app-vendorview',
  templateUrl: './vendorview.component.html',
  styleUrls: ['./vendorview.component.scss']
})
export class VendorviewComponent implements OnInit {

  public vendor: VendorStruct;
  public vendors: Array<VendorStruct>;
  public vendorTemplate: Array<TemplateFormat>;
  public filteredData: Array<VendorStruct>;

  constructor(private mockservice: MockService) { }

  ngOnInit() {
    const newVendor = new Vendor({
      vendorName: '',
      status: '',
      city: '',
      state: '',
      country: '',
      pincode: '',
      contactNo: '',
      contractNo: ''
    });

    this.vendor = newVendor.vendor;
    this.vendors = this.mockservice.listVendors;
    this.vendorTemplate = this.mockservice.getVendorTemplate;
    this.filteredData = this.vendors;
  }

  filter(data) {
    this.filteredData = data;
  }

  save(vendor) {
    console.log('vendor');
  }
}
