import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { Notification, NotificationType } from '../models/notification';


@Injectable({
  providedIn: 'root'
})
export class NotificationService {

  private subject = new Subject<Notification>();
  private keepAfterRouteChange = false;

  constructor() { }

  getAlert(): Observable<any> {
    return this.subject.asObservable();
  }

  success(message: string) {
    this.alert(NotificationType.Success, message);
  }

  error(message: string) {
    this.alert(NotificationType.Error, message);
  }

  info(message: string) {
    this.alert(NotificationType.Info, message);
  }

  warn(message: string) {
    this.alert(NotificationType.Warning, message);
  }

  alert(type: NotificationType, message: string) {
    this.subject.next(<Notification>{ type: type, message: message });
  }

  clear() {
    // clear alerts
    this.subject.next();
  }
}
